.OtherBonusesImages {
    position: relative;
    width: 60px;
    height: 60px;
}

.OtherBonusesImage img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.OtherBonusesImage {
    position: absolute;
    top: 0;
    left: 0;
}
.OtherBonusesImage1 {
    position: absolute;
    z-index: 1;
}
.OtherBonusesImage2 {
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.OtherBonusesWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.css-o4b71y-MuiAccordionSummary-content.Mui-expanded {
    margin: 0;
}
.css-o4b71y-MuiAccordionSummary-content {
    margin: 0 !important;
    margin-top: 10px !important;
}
.css-15v22id-MuiAccordionDetails-root {
    padding: 0 !important;
}

