.Bonus {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px 0px 10px;
    background: var(--tg-theme-bg-color);
    position: relative;
}
.BonusWrapper::after {
    content: "";
    position: absolute;
    width: 101%;
    height: 1px;
    background-color: #000;
    bottom: -26px;
    right: -10px;
}
.BonusWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
}
.BonusImg {

}
.BonusImg img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
}
.BonusName {
    font-size: 18px;
    padding-left: 5px;
    font-family: Tahoma;
}

.BonusBalace {
    font-size: 18px;
    font-weight: 500;
    font-family: Roboto;

}